<template>
   <div>
    <h2>Register</h2>
    <div class="row">
      <div class="card mx-auto">
        <div class="card-header text-white bg-primary">
          <h3>Register</h3>
          <div class="card-body">
            <form @submit.prevent="registerUser">
              <div class="form-group">
                <label for="username">Username</label>
                <input type="text" name="username" v-model="username" id="username" class="form-control" placeholder="username">
              </div>

              <div class="form-group">
                <label for="name">Name</label>
                <input type="text" name="name" v-model="name" id="name" class="form-control" placeholder="username">
              </div>

              <div class="form-group">
                <label for="email">Email</label>
                <input type="text" name="email" v-model="email" id="email" class="form-control" placeholder="email">
              </div>

              <div class="form-group">
                <label for="password">Password</label>
                <input type="text" name="password" v-model="password" id="password" placeholder="Password" class="form-control">
              </div>

              <div class="form-group">
                <label for="confirm_password">Password</label>
                <input type="text" name="confirm_password" v-model="confirm_password" id="confirm_password" placeholder="Password" class="form-control">
              </div>

              <button class="btn btn-danger mt-1">Register</button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <router-link to="/login" class="card-link">Registered?</router-link>
            </form>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  data(){
      return {
        username: "",
        name: "",
        email: "",
        password: "",
        confirm_password: ""
      }
  },
  methods:{
    ...mapActions(['register']),
    registerUser(){
      let user = {
        username: this.username,
        password: this.password,
        confirm_password: this.confirm_password,
        email: this.email,
        name: this.name
      };
      this.register(user).then(res =>{
        if(res.data.success){
          this.$router.push("login");
        }
      })
    }
  }
}
</script>

<style>
.card{
  width: 60%;
  border-radius: 0;
}

.btn{
  border-radius: 0;
}

.form-control {
  border-radius: 0;
}

</style>